.home-news
	padding: 4rem 0
	background: #171e26
	.title-h1-white
		color: #ffffff
		font-weight: 400
	.news-wrapper 
		.news-title
			padding-bottom: 2rem
		.news-slider
			position: relative
			.swiper-container
				.swiper-wrapper
					.swiper-slide 
						.img
							height: 240px
							width: auto 
							overflow: hidden
							img 
								width: 100%
								height: 100%
								object-fit: cover
								transition: all 0.4s ease-in-out
						figcaption 
							time 
								font-size: 12px 
								color: #f5f5f5
							.news-title
								display: block
								color: #929598
								transition: .4s ease-in-out
								padding: 0
						&:hover 
							figcaption
								.news-title 
									color: #ffffff
							.img 
								img 
									transform: scale(1.10)
			.swiper-navigation
				width: 110%
				display: flex
				align-items: center
				position: absolute
				top: 50%
				left: 50%
				transform: translate(-50%,-50%)
				z-index: 99
				.news-button-next,.news-button-prev
					width: 35px
					height: 35px
					display: flex
					border: none
					outline: none
					em 
						color: #999999
						font-size: 40px
						font-weight: bold
					&:hover 
						em
							color: #8c2004
							transition: all .4s ease-in-out
				.news-button-next 
					margin-left: auto
				@media (max-width: 992px)
					top: 110%
					width: 50px

			.swiper-pagination 
				display: none
			@media (max-width: 876px)
				.news-button-next
					display: block
					bottom: -2rem
					right: 35%
				.news-button-prev
					display: block
					bottom: -2rem
					left: 35%
				