.career-detail-section 
	padding: 3.75rem 0
	.main-button
		cursor: pointer
	.career-detail
		.title-h1-grey
			margin-left: 30px
			font-weight: 500!important
			@media (max-width: 768px)
				margin-left: 0
				padding-left: 20px
				&:before 
					left: 0
		
		.mota
			font-size: 18px
			text-transform: unset!important
			font-weight: 500
		.main-button
			padding-left: 3rem
			position: relative
			font-size: 18px
			font-weight: 700
			color: #333333
			text-transform: uppercase
			display: inline-block
			margin-top: 20px
			&:hover
				&::after
					transform: translateY(-50%), translateX(3px)
			&:after
				font: normal normal normal 18px/1 Material Design Icons
				content: '\f142'
				font-size: 20px
				color: #333333
				position: absolute
				right: -2rem
				top: 50%
				transform: translateY(-50%)
			&:before 
				content: ''
				display: block
				position: absolute
				width: 15px
				height: 15px
				background: #333333
				left: 0
				top: 50%
				transform: translateY(-50%)

		.full-content
			line-height: 1.6
			p,li,h4
				margin-bottom: 10px
				font-size: 16px
				strong 
					font-size: 16px
					display: block
					padding-top: 10px
			ul
				list-style-type: none
				li
					position: relative
					&:before
						content: '-'
						color: #999999
						padding-right: 20px
		.other-news
			@media (max-width: 1025px)
				margin-top: 20px

			.item
				padding: 10px 0
				border-bottom: 1px solid #f1f5f6
				h3
					a 
						font-weight: 600
						font-size: 14px
						color: #333333
				span 
					font-size: 13px
					color: #999999
					font-weight: 400
				&:hover 
					h3 
						a 
							color: #8c0000
							transition: all .3s ease-in-out
	.form-apply
		height: 740px!important
		display: none
		margin-top: 40px
		padding: 0
		box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1)
		border-top: 10px solid white
		border-bottom: 10px solid white
		@media (min-width: 623px)
			height: 420px!important
		iframe
			border: 0
			background: white
			width: 100%
			height: 100%
.canhcam.news-detail-page.vi-vn
	.nav-career,.career-banner
		display: none
.carrer-dt-page
	@media (max-width: 768px)
		.sub-title
			display: none!important